@mixin advertisers {
  @include section-padding;
  @include section-fitscreen;
  @include colors(white-on-blue);

  display: flex;
  flex-direction: column;
  justify-content: center;

  .article {
    @include article;

    &__title,
    &__subtitle {
      padding: $lem / 4 0;
    }

    &__category {
      @include colors(green-on-transparent);
    }

    &__excerpt {
      @include colors(gray-format-on-transparent);
    }
  }
}
