@mixin slider2--size($width, $margin, $button_width) {
  $height: calc(#{$width} / 1.7778 + #{$lem / 4});
  height: $height;
  overflow: hidden;

  &__slides {
    .slide {
      width: $width;

      @for $i from 1 to 11 {
        &:nth-of-type(#{$i}) {
          transform: translateX(($width + $margin) * ($i - 1));
        }
      }
    }
  }

  &__navigation {
    top: calc(50% - #{$button_width} / 2);

    .button {
      @include circle($button_width);

      .triangle {
        @include triangle($size: $button_width / 3, $color: $white);
      }
    }
  }
}
